/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 180px;
						margin: 0 10px 20px 0;
					}
				}


				.coupon,
				> input,
				> button{
					float: none;
					display: block;
				}
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {
		
		table.cart.shop_table {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
				.mkdf-quantity-buttons {
					.mkdf-quantity-input {
						height: 40px;
						width: 40px;
						line-height: 38px;
						margin-right: 20px;
					}

					.mkdf-quantity-plus,
					.mkdf-quantity-minus{
						height: 20px;
						width: 20px;
						line-height: 18px;
					}
				}
			}
		}

		.cart-collaterals .wc-proceed-to-checkout{
			a.button{
				padding: 16px;
				width: 100%;
				text-align: center;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */