/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $laptop-landscape-plus-pixel) and (max-width: $laptop-landscape-large) {

	.mkdf-woo-single-page {

		.woocommerce-tabs {

			ul.tabs > li a {
				padding: 13px 30px 12px;
			}
		}
	}
}

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .mkdf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .mkdf-woo-single-page {
        
        .mkdf-single-product-content {
            
            .images,
            .mkdf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .mkdf-single-product-summary {
                margin: 30px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}

@include phone-landscape {
    
    .mkdf-woo-single-page {
	
	    &.mkdf-woo-single-thumb-on-left-side {
		
		    .mkdf-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .mkdf-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }
    }
}


@include phone-portrait {
    .mkdf-woo-single-page {
        .woocommerce-tabs {
            ul.tabs>li {
                display: block;
                border-right-width: 1px;
                border-bottom-width: 0;

                &:last-of-type {
                    border-bottom-width: 1px;
                }
            }
        }
    }
    .woocommerce-page .mkdf-content .button[type=submit].single_add_to_cart_button {
        padding-left: 25px;
        padding-right: 25px;
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */